.About{
    margin:250px auto;

    width: 80%;
    height: 550px;
    // border: 3px solid red;
    background-color: #252420;
    position: relative;
    top: 100px;
    border-radius: 30px;
    
    
    z-index: -1000;

    .about__wrapper{
        
        width: 50%;
        height: 100%;
        

        // border: 2px solid blue;
        .about__item{
            width: 50%;
            h1{
                width: 500px;
                height: 80px;
                font-weight: 700;
                font-size: 40px;
                padding-top: 70px;
                padding-left: 80px;
                color: white;
            }
            p{
                width: 440px;
                height: 108px;
                margin-top: 160px;
                margin-left: 80px;
                font-weight: 400;
                font-size: 18px;
                line-height: 27px;
                color: white;
                
            }
            // .btn{

            //     &:focus{
            //         background-color: white;
            //         color: #FF7500;
            //         // border: 2px solid #FF7500;
            //     }
            // }

            button{
                width: 189px;
                height: 47px;
                margin-top: 50px;
                margin-left: 80px;
                border-radius: 8px;
                border: none;
                background-color: #FF7500;
                font-size: 15px;
                color: white;
                line-height: 22.5px;

                 &:active {
                    background-color: #f89844;
                   
                    // border: 2px solid #FF7500;
                }
            }
           
            img{
                margin-top: -2000px ;
                margin-left: 630px;
            }

        }
    }

    
}