header{
    position: fixed;
    width: 100%;
    padding: 1rem;
    background-color: white;

    &.dark{
        background-color: black;
        color: white;
    }

    nav{
        // padding: 22px 0px;
        display: flex;
       align-items: center;
       max-width: 1200px;
       margin: 0 auto;
       justify-content: space-between;


       ul{
        display: flex;
        align-items: center;
        gap: 1rem;
        .text{
            :focus{
                border-bottom: 2px solid #FF7500;
                color: #FF7500;
            }
        }

        li{
            list-style: none;

            

            a{
                text-decoration: none;
                color: black;
                font-size: 13px;
                text-transform: uppercase;
                font-weight: bolder ;

                &.dark{
                    color: white;
                }
               

               
               
            }
            .btn{
                &:focus{
                    background-color: white;
                    color: #FF7500;
                    border: 2px solid #FF7500;
                    
                }
            }
            button{
                width: 189px;
                height: 47px;
                background-color: #FF7500;
                border-radius: 10px;
                border: none;
                color: white;
                font-weight: 700;

                

                
            }
            
            
        }
       }
    }
}