    top: 100px;
    background-color: #252420;
    z-index: -1000;

    .hero__wrapper{
        width: 50%;
        height: 100%;
        // border: 2px solid blue;
        .hero__item{
            width: 50%;
            h1{
                width: 600px;
                height: 80px;
                font-weight: 700;
                font-size: 40px;
                padding-top: 120px;
                padding-left: 170px;
                color: white;
            }
            p{
                width: 440px;
